<template>
  <div class="security">
		<h2>安全中心</h2>
		<div class="plist number">
			<h3>账户保护</h3>
			<ul class="con">
				<li><img src="../../assets/images/mail.png"/></li>
				<li>
					<h4>安全邮箱 <span>未绑定</span></h4>
					<!-- 绑定成功后的样式 -->
					<!-- <h4>安全邮箱 121****569@qq.com</h4> -->
					<p>可以作为登录、找回密码以及其他安全保护</p>
				</li>
				<li>
					<router-link v-if="!isBindMail" to="/bindmail">修改邮箱</router-link>
					<router-link v-if="isBindMail" to="/unbindmail" @hover="changeBtnText">{{bindMailBtnText}}</router-link>
				</li>
				<div class="clear"></div>
			</ul>
			<ul class="con">
				<li><img src="../../assets/images/telhelp.png"/></li>
				<li>
					<h4>安全手机 131****1597</h4>
					<p>可以享受手机相关的登录、安全机提醒服务</p>
				</li>
				<li>
					<router-link v-if="!isBindMail" to="/changetel">更改手机</router-link>
					<router-link v-if="isBindMail" to="/unbindmail" @hover="changeBtnText">{{bindMailBtnText}}</router-link>
				</li>
				<div class="clear"></div>
			</ul>
		</div>
		<div class="plist change">
			<h3>密码管理</h3>
			<ul class="con">
				<li><img src="../../assets/images/change.png"/></li>
				<li><h4>修改密码</h4><p>可以作为登录、找回密码以及其他安全保护</p></li>
				<li><router-link to="/modifypwd">立即修改</router-link></li>
				<div class="clear"></div>
			</ul>
		</div>
		<div class="plist find">
			<ul class="con">
				<li><img src="../../assets/images/find.png"/></li>
				<li><h4>找回密码</h4><p>可以享受手机相关的登录、安全及提醒服务</p></li>
				<li><router-link to="/findpwd">立即找回</router-link></li>
				<div class="clear"></div>
			</ul>
		</div>
	</div>
</template>
<script>
export default {
	name: 'pcsafecenter',
	data() {
    return {
			isBindMail: false,
			bindMailBtnText: '已绑定'
		}
	},
	computed: {
	},
	created: function() {
		this.getMailStatus();
	},
	methods: {
		// 查询邮箱是否绑定
    getMailStatus(){
      this.$axios.post('/pcgc/bingding')
      .then((res) => {
				console.log(res)
        if(res.data.code === '1'){
					console.log(typeof res.data.is_mail)
					if(res.data.is_mail){
						this.isBindMail = true;
					}
				} else {
					alert(res.data.msg)
				}
			})
			.catch((err) => {
				throw new Error('err'+err)
			})
		},
		changeBtnText(){
			console.log('11')
			this.bindMailBtnText = '解除绑定'
		}
	}
}
</script>
<style lang="scss" scoped>
	.security{
		width:900px;
		height: 672px;
		background-color: #fff;
		margin: 50px auto;
		box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
		h2{
			font-size: 28px;
			line-height:90px;
			font-weight:400;
			color: #2e2e2e;
			text-align: center;
			border-bottom:1px solid #ebebeb;
		}
		.plist{
			width:90%;
			margin:0 auto;
			h3{
				font-size: 16px;
				color: #2e2e2e;
				padding:50px 0 0 0;
			}
			ul.con{
				padding:25px 0;
				border-bottom: 1px solid #ebebeb;
				li{
					height:53px;
					display: inline-block;
					vertical-align: middle;
					&:nth-child(1){
						width:53px;
						height: 53px;
						img{
						width:100%;
						height: 100%;
						}
					} 
					&:nth-child(2){
						width:75%;
						margin-left: 2%;
					}
					&:nth-child(3){
						line-height: 53px;
						a{
							width:110px;
							height:30px;
							border:1px solid #e5e5e5;
							border-radius:5px;
							color: #4d4d4d;
							font-size: 14px;
							display: inline-block;
							text-align: center;
							line-height: 30px;
							&:hover{
								background: #e5e5e5;
							}
						}
					}
					h4{
						font-size: 16px;
						margin:0;
						font-weight: 400;
						margin:8px auto;
						color: #2e2e2e;
						display: inline-block;
						span{
							font-size: 12px;
							color: #EA6457;
							&:before{
								content:'';
								width:17px;
								height: 17px;
								margin: 0px 6px 0px 11px;
								display: inline-block;
								vertical-align: middle;
								background-size: 100% 100%;
								background-image: url(../../assets/images/nobind.png);
							}
						}
					}
					p{
						font-size: 12px;
						color: #878787;
					}
				}
			}
			&:last-child{border: none;}
		}
		.find{
			ul.con{
				border:none;
			}
		}
	}
</style>


